export interface IFormValidatorOptions {
    maxLength?: number;
    minLength?: number;
    minAndMaxLength?: { min: number; max: number; };
    email?: boolean;
    availableCharacters?: boolean;
    nonSpaceCharacter?: boolean;
    customRegex?: { regex: RegExp; message: string; };
}

export interface IFormValidatorResponseItem {
    key: keyof IFormValidatorOptions;
    message: string;
    isValid: boolean;
}

export interface IFormValidator {
    validate(value: string, options: IFormValidatorOptions): Array<IFormValidatorResponseItem>;
}

export const IFormValidator = Symbol.for("IFormValidator");