props.orgGroup.features?.appVersion);\n    const emit = defineEmits([\"close\"]);\n\n\n    const validations = ref<Record<\"name\" | \"description\", IIDEInputDefinitions[\"validations\"]>>({\n        name: { minAndMaxLength: { min: 3, max: 25 }, availableCharacters: true, nonSpaceCharacter: true },\n        description: { minAndMaxLength: { min: 3, max: 100 }, availableCharacters: false, nonSpaceCharacter: false },\n    });\n    function orgGroupNameValidationStatus(isValid: boolean) {\n        isOrgGroupNameValid.value = isValid\n\n    }\n    const isThereUpdate = () => {\n        if(props.orgGroup) {\n            return !(props.orgGroup!.name == name.value && props.orgGroup!.description == description.value && props.orgGroup.features?.appVersion == appVersion.value)\n        }\n        else {\n            return true;\n        }\n    }\n    function addOrUpdateOrgGroup() {\n        executor.execute( async () => {\n            if(props.orgGroup) {\n                const features = {appVersion: appVersion.value!};\n                await backofficeUseCase.updateOrganizationGroup({ID: props.orgGroup.ID, organizationGroup: {name: name.value!, description: description.value, features}});\n            }\n            else {\n                await backofficeUseCase.addOrganizationGroup({name: name.value!, description: description.value});\n            }\n            close();\n        }, {loading: true})\n    }\n    function descriptionValidationStatus(isValid: boolean) {\n        isDescriptionValid.value = isValid\n\n    }\n    function setNameValue(value: string) {\n        name.value = value\n    }\n\n    function setDescValue(desc: string) {\n        description.value = desc\n    }\n    function close() {\n        emit(\"close\");\n    }\n\n    const addButtonDisabled = computed(() => !isOrgGroupNameValid.value || !isDescriptionValid.value || !isThereUpdate());\n\n</script>\n<template>\n    <div class=\"container\">\n        <div class='updateOrCreateTextAreaDiv'>\n            <IDEInput :isRequired=\"true\" elementId=\"nameInput\" @getValue=\"setNameValue\" @validateStatus=\"orgGroupNameValidationStatus\" placeholder=\"\"\n                :type=\"'text'\" :value=\"props.orgGroup?.name\"  info-text=\"Organization Group Name\" :validations=\"validations.name\" />\n        </div>\n        <div class='updateOrCreateInputDiv'>\n            <IDEInput :isRequired=\"false\" elementId=\"descriptionInput\" @validateStatus=\"descriptionValidationStatus\" placeholder=\"\" \n                :type=\"'textarea'\" :value=\"props.orgGroup?.description\"   info-text=\"Description\" :validations=\"validations.description\" @getValue=\"setDescValue\" />\n        </div>\n        <div class=\"org-group-prop\">\n            <label>Features</label>\n            <div class=\"update-field\">\n                <span class=\"update-field-name\">App Version</span>\n                <input class=\"input-switch round\" type=\"checkbox\" v-model=\"appVersion\" />\n            </div>\n        </div>\n        <div class='updateOrCreateRow'>\n            <div class=\"col\">\n                <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n                <button :style=\"{ color: addButtonDisabled ? '#757575' : '#FFFFFF' }\" class='updateOrCreateBtn'\n                    @click=\"addOrUpdateOrgGroup()\" :disabled=\"addButtonDisabled\">{{\n                            orgGroup ? \"SAVE\" : \"CREATE\"\n                    }}</button>\n            </div>\n        </div>\n    </div>\n\n</template>\n<style scoped>\n    * {\n        font-family: \"Roboto\";\n    }\n    .container {\n        padding: 32px 20px 20px\n    }\n    .updateOrCreateTextAreaDiv {\n        height: 77px;\n    }\n    .updateOrCreateTextarea {\n        width: 100%;\n        outline: none;\n        height: 120px;\n        border: 1px solid #A1A1A1;\n        border-radius: 4px;\n        opacity: 1;\n        resize: none;\n        margin-top: 3px;\n    }\n\n    .updateOrCreateInputDiv {\n        height: 173px;\n\n    }\n\n    .updateOrCreateInput {\n        width: 100%;\n        outline: none;\n        height: 40px;\n        border: 1px solid #A1A1A1;\n        border-radius: 4px;\n        opacity: 1;\n        margin-top: 3px;\n    }\n\n    .updateOrCreateRow {\n        display: flex;\n        text-align: end;\n    }\n\n    .updateOrCreateBtn {\n        background-color: #151233;\n        color: #fff;\n        border-radius: 4px;\n        /* width: 72px; */\n        height: 40px;\n        font-weight: 500;\n        font-size: 16px;\n        padding: 8px 15px;\n    }\n    .cancel-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #757575;\n        padding: 8px 16px;\n        border-radius: 4px;\n        margin-right: 4px;\n        border: none;\n        background: transparent;\n    }\n    ::placeholder {\n        padding-left: 10px;\n        padding-top: 5px;\n        color: #A1A1A1;\n        font-style: normal;\n        font-family: \"ROBOTO\";\n        letter-spacing: 0px;\n        font-size: 14px;\n        opacity: 1px;\n    }\n    .org-group-prop {\n        margin-bottom: 20px;\n    }\n\n    .org-group-prop label {\n        font-weight: 500;\n        font-size: 16px;\n        line-height: 24px;\n        color: #151233;\n        margin-bottom: 4px;\n    }\n    .update-field-name {\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n\n    .update-field {\n        display: flex;\n        align-items: center;\n        margin-bottom: 12px;\n    }\n    .input-switch {\n        position: relative;\n        cursor: pointer;\n        top: 0;\n        bottom: 0;\n        background-color: #ccc;\n        -webkit-transition: 0.4s;\n        transition: 0.4s;\n        margin-right: 13px;\n        margin-left: auto;\n    }\n\n    .input-switch:after {\n        position: absolute;\n        content: \"\";\n        height: 14px;\n        width: 14px;\n        background-color: white;\n        -webkit-transition: 0.4s;\n        transition: 0.4s;\n        border-radius: 50%;\n    }\n\n    .input-switch:disabled:after {\n        background-color: #757575;\n    }\n\n    .input-switch:checked {\n        background-color: #151233;\n    }\n\n    input[type=checkbox] {\n        -webkit-appearance: none;\n        -moz-appearance: none;\n        appearance: none;\n        -webkit-tap-highlight-color: transparent;\n        cursor: pointer;\n    }\n\n    .input-switch:checked:after {\n        transform: translatex(16px);\n    }\n\n    .input-switch.round::after {\n        left: 3px;\n        border-radius: 50%;\n    }\n\n    .input-switch.round {\n        width: 36px;\n        height: 20px;\n        border-radius: 34px;\n        padding: 3px;\n    }\n\n</style>"],"sourceRoot":""}]);
